<template>
  <div>
    <van-nav-bar left-arrow title="提交订单"  @click-left="onClickLeft" />
    <van-contact-card type="edit" :name="currentContact.name" :tel="currentContact.tel" @click="onEdit" />
    <van-card num="2" price="2.00" title="商品标题" thumb="https://img01.yzcdn.cn/vant/ipad.jpeg" />
    <van-cell-group>
      <van-coupon-cell :coupons="coupons" :chosen-coupon="chosenCoupon" @click="showList = true" />
      <van-cell title="发票" is-link />
      <van-cell title="商品价格" :value="price" />
      <van-cell title="运费" :value="price" />
      <van-field v-model="message" rows="2" autosize label="留言" type="textarea" maxlength="50" placeholder="请输入留言"
                 show-word-limit />
    </van-cell-group>

    <van-submit-bar :price="3050" button-text="提交订单" @submit="onSubmit">

      <template #tip>
        你的收货地址不支持同城送, <span @click="onClickEditAddress">修改地址</span>
      </template>
    </van-submit-bar>

    <!-- 优惠券列表 -->
    <van-popup v-model="showList" round position="bottom" style="height: 90%; padding-top: 4px;">
      <van-coupon-list :coupons="coupons" :chosen-coupon="chosenCoupon" :disabled-coupons="disabledCoupons"
                       @change="onChange" @exchange="onExchange" />
    </van-popup>

  </div>
</template>

<script>
const coupon = {
  available: 1,
  condition: '无使用门槛\n最多优惠12元',
  reason: '',
  value: 150,
  name: '优惠券名称',
  startAt: 1489104000,
  endAt: 1514592000,
  valueDesc: '1.5',
  unitDesc: '元',
};
export default {
  name: "addOrder",
  data() {
    return {
      showList: false,
      chosenCoupon: -1,
      coupons: [coupon],
      disabledCoupons: [coupon],
      checked: false,
      currentContact: {
        name: '张三',
        tel: '13000000000',
      },
      shop_card: "未选择",
      price: 50,
      message: '',
    };
  },
  methods: {
    //返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    onChange(index) {
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange(code) {
      this.coupons.push(coupon);
    },
    onEdit() {
      this.$router.push('address_list')
    },
    onSubmit() {
      //跳到支付页面
      this.$router.push('payment');
    },
    onClickEditAddress() {

    },
  },
}
</script>

<style scoped>

</style>



